"use strict"

document.addEventListener('click', documentActions)

export function initEffects() {
   initScrollHeader()
   showList()
}

function initScrollHeader() {
   const header = document.querySelector('.header');
   if (!header) return;

   let lastScroll = window.scrollY;
   let downStart = window.scrollY;

   const OFFSET = 50;
   const DELTA = 8;
   const HIDE_AFTER = 40;

   const onScroll = () => {
      // 1. –ó–ê–•–ò–°–¢: –Ø–∫—â–æ –º–µ–Ω—é –≤—ñ–¥–∫—Ä–∏—Ç–µ, –ø–æ–≤–Ω—ñ—Å—Ç—é —ñ–≥–Ω–æ—Ä—É—î–º–æ –ª–æ–≥—ñ–∫—É —Å–∫—Ä–æ–ª—É
      // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ –∫–ª–∞—Å –Ω–∞ html –∞–±–æ –Ω–∞ —Å–∞–º–æ–º—É —Ö–µ–¥–µ—Ä—ñ
      if (document.documentElement.classList.contains('menu-open') ||
         header.classList.contains('menu-open')) {
         return;
      }

      const current = window.scrollY;
      const diff = current - lastScroll;

      if (Math.abs(diff) < DELTA) return;

      // –í–µ—Ä—Ö —Å—Ç–æ—Ä—ñ–Ω–∫–∏
      if (current <= OFFSET) {
         header.classList.remove('scrolled', 'visible');
         // –°–∫–∏–¥–∞—î–º–æ —ñ–Ω–ª–∞–π–Ω-—Å—Ç–∏–ª—ñ, —è–∫—â–æ –≤–æ–Ω–∏ –¥–æ–¥–∞–≤–∞–ª–∏—Å—è —á–µ—Ä–µ–∑ JS
         header.style.transform = '';
         downStart = current;
         lastScroll = current;
         return;
      }

      // –°–∫—Ä–æ–ª –≤–Ω–∏–∑
      if (diff > 0) {
         if (current - downStart > HIDE_AFTER) {
            header.classList.add('scrolled');
            header.classList.remove('visible');
         }
      }
      // –°–∫—Ä–æ–ª –≤–≥–æ—Ä—É
      else {
         header.classList.add('scrolled', 'visible');
         downStart = current;
      }

      lastScroll = current;
   };

   window.addEventListener('scroll', onScroll, { passive: true });
}

// ===========================================================================================
// -----------------------------
// MENU-BURGER
// -----------------------------
function documentActions(e) {
   const targetElement = e.target
   if (targetElement.closest('.icon-menu')) {
      document.body.classList.toggle('menu-open')
      document.body.classList.toggle('scroll-lock')
      document.documentElement.classList.toggle('menu-open')
   }
}


function showList() {
   const items = document.querySelectorAll('.row-menu');

   items.forEach(item => {
      const icon = item.querySelector('.row-menu__icon');
      const wrap = item.querySelector('.row-menu__wrap');

      icon.addEventListener('click', () => {
         icon.classList.toggle('icon-active');
         wrap.classList.toggle('open');
      });
   });
}
